import React from 'react'

import { graphql } from "gatsby"

import Layout from "../components/Layout"
import SEO from "../components/seo"

const About = ({ data }) =>  {
    return (
        <Layout>
            <SEO title="A" />
            <h1>About Me</h1>
            <ul className="resources-list">
                <li><a href={data.cv.publicURL}>My CV</a></li>
            </ul>
            <div>
                <p>
                    I just like making stuff and thinking about stuff. 
                    Hoping that one day I'll make something that makes 
                    some sort of a difference.
                </p>
                <p>You can get a hold of me through my <a href="/contact">contact</a> page.</p>
            </div>
        </Layout>
    )
}


export const aboutQuery = graphql`
  query {
    cv: file(relativePath: { eq: "cv.pdf" }) {
        name
        publicURL
      }
  }
`

export default About
